
.cancel-appointment{
    .motion-div{
        width: 100%;
        height: 100%;
    }
    text-align: center;
    padding-top: 1rem;
    width: 100%;
    height: 100%;
    transition: 0.5s;
    .cancelation-title-phone{
        margin-bottom: 1rem;
        .cancel-form-title{
            margin-bottom: 1rem;
            color : #172b4d;
        }
    }
    // fieldset{
    //     // width: 300px;
    // }
    .table-wrapper{
        .table-title{
            text-align: center;
            margin-bottom: 1rem;
        }
    }

    .table-wrapper{
      .apointment-details{
        text-align: right;
        color :#e91e63;
        background-color:lighten(#e91e63, 45%);
        border: 0.2px solid #f0d1dbd3;
        border-radius: 5px;
        .table-cell{
            border-bottom: 2.5px solid white;
            padding: 20px;
        }
        .last-cell{
            padding: 20px;
            }
        }
    }
    i{
        color:white;
        font-size: 1.1rem;
        margin-right:10px;
    }
    .trash-btn{
        bottom: 0;
        position: fixed;
        color :white;
        border: none;
        // border-radius: 5px;
        outline: none;
        width: 100%;
        padding: 10px;
        font-size: 1rem;
        background-color: #e91e63;
        cursor: pointer;
        left: 50%;
        margin-left: -50%; /* Negative half of width. */
    }
}

