@font-face {
    font-family: Tinos;
    src: url('../fonts/static/Heebo-Regular.ttf');
  }

html {
    height: 100%;
}

* {
    box-sizing: border-box;
    font-family: Tinos;
}

body {
    color: #172b4d;
    margin: 0;
    font-size: 1rem;
    background-color: rgba(236, 236, 236, 0.322);
    height: 100%;
    overflow: hidden;
}


